@media (max-width: 705px) {
    @import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');


    #app {
      height: 100%;
      /* overflow-y: hidden; */
    
    }
    
    ul,
    li,
    ol {
      text-decoration: none;
      list-style-type: none;
    }
    
    html,
    body {
      position: relative;
      height: 100%;
      font-family: 'Anton', sans-serif;
    }
    
    body {
      background: white;
      font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
      font-size: 14px;
      color: #000;
      margin: 0;
      padding: 0;
    }
    
    .header {
      width: 100%;
      /* height: 500px; */
      background-color: transparent;
      border: 1px solid transparent;
      position: fixed;
      z-index: 200;
      /* position:sticky; */
      margin: -1px;
    }
    
    .header .card {
      width: 101%;
      /* height: 500px; */
      background-color: transparent;
      border: 1px solid transparent;
      /* margin-top: -3px; */
      z-index: -1;
      /* transition: 5s; */
      margin: -1px;
    }
    
    
    .header .card-header:first-child {
      width: 20%;
      height: 100px;
      position: absolute;
      z-index: 150;
      border: 1px solid transparent;
      background-color: transparent;
      /* top: 10%; */
      left: 50%;
      padding-top: 35px;
      transform: translate(-50%, -10%);
    
    
    }
    
    
    .header .card-header {
      width: 100px;
      height: 100px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid transparent;
      cursor: pointer;
    
    
    }
    
    .header .card-header button {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid transparent;
      background-color: transparent !important;
      cursor: pointer;
    
    }
    
    .header .logo {
      width:70px;
      height: 70px;
      margin-top: 30px;
      filter: brightness(10000) invert(1);
      cursor: pointer;
    
    }
    
    .header .logo-after {
      width: 50px;
      height: 50px;
      margin-top: 30px;
      filter: none !important;
      cursor: pointer;
    }
    
    .header .acoordion-collapse {
      width: 100%;
      height: 500px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid transparent;
      background-color: transparent;
    
    }
    
