    .swiper {
      width: 100%;
      height: 1700px;
    }
    
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background-color: transparent;
    
      /* Center slide text vertically */
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
    
    .txt {
      width: 90%;
      /* height: 900px; */
      /* height: 50px; */
