      position: absolute;
      /* flex-direction: column; */
      z-index: 180;
      font-size: 14px;
      /* margin-top: 15px; */
      opacity: 1;
      transition: 0.5s;
      /* display:none; */
    }
    
    .txt-after {
      opacity: 0;
      transition: 0.5s;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    
    .txt ul li:nth-child(1) {
      font-weight: bolder;
    }
    
